body {
  height: 1600px;
  width: 944px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  background-color: black;
  background-image: url(../src/mj-background.jpg);
  background-size: auto;
  background-position: center top;
  background-repeat: no-repeat;
}

.main-content {
  display: block;
  position: relative;
  height: 1070px;
  width: 810px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

header {
  display: grid;
  grid-template-rows: 9fr 1fr;
  grid-template-columns: 1fr 2fr 1fr;
  height: 90px;
  width: 750px;
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: lightyellow;
  border-radius: 20px;
}

header button {
  top: -10px;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
}

header div {
  text-align: center;
  position: relative;
  top: -10px;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 3;
}

header h1 {
  display: inline;
  position: relative;
  top: -15px;
  color: #809848;
}

.header-tile {
  display: inline;
  position: relative;
  top: 10px;
  height: 100px;
  width: 90px;
  transform: rotate(-10deg);
}

header p {
  position: relative;
  padding-left: 10px;
  top: -30px;
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}

header .score-text {
  float: right;
  position: relative;
  right: -10px;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 3;
  grid-column-end: 4;
  font-size: 3rem;
  text-align: right;
  margin-top: 20px;
  margin-bottom: 0px;
}

.list-img {
  position: relative;
  right: 350px;
  top: 25px;
}

.board-position {
  display: grid;
  height: 1075px;
  grid-template-columns: 1.5fr 2.5fr 1.5fr;
  grid-template-rows: 0.3fr 1fr 1.5fr 1fr 2fr;
}

.english-font {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-style: extralight;
  text-align: center;
  color: black;
}

.chinese-font {
  font-family: "Noto Serif HK", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: black;
}

.large-font {
  font-size: 4rem;
}

.medium-font {
  font-size: 2.6rem;
}

.small-font {
  font-size: 2rem;
}

.tiny-font {
  font-size: 1.5rem;
}

.message-text {
  display: flex;
  align-items: center;
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 2;
  grid-column-end: 3;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  text-align: center;
  color: black;
  padding-left: 20px;
  padding-right: 20px;
}

@keyframes blink {
  0% {
      opacity: 1;
  }

  50% {
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}

.blinking-text {
  animation: blink 1s infinite;
}

.restart-img {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
  position: relative;
  height: 70px;
  width: 70px;
  left: 15px;
  top: -115px;
  cursor: pointer;
}

.east-position {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 2;
}

.south-position {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 2;
  grid-column-end: 3;
}

.west-position {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 3;
  grid-column-end: 4;
}

.north-position {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}

.error-text {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-row-start: 5;
  grid-row-end: 6;
  grid-column-start: 1;
  grid-column-end: 4;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  text-align: center;
  color: red;
}

.tile_image {
  display: block;
  height: 165px;
  width: 135px;
  margin-right: auto;
  margin-left: auto;
  transform: rotate(-5deg);
}

.player-position {
