  font-family: "Noto Serif HK", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-size: 2rem;
  color: black;
  margin-top: 10px;
  margin-bottom: 10px;
}

.circle-with-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: lightyellow;
  /*background: linear-gradient(yellow, lightyellow);*/
