}

.player-card {
  position: relative;
}

.player-text {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 2rem;
  width: 150px;
  margin-right: auto;
  margin-left: auto;
  color: brown;
  border-radius: 20px;
}

.score-text {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 1.1rem;
  font-family: "Noto Serif HK", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: extralight;
  font-size: 1.5rem;
