.backdrop {
  backdrop-filter: blur(10px);
  height: 1440px;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  container-type: size;
}

.dialog {
  background: #9bcac4;
  height: 670px;
  width: 800px;
  border-radius: 2rem;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  max-width: 100%;
  z-index: 2;
  padding: 20px;
  animation-duration: 1s;
  animation-name: slide-in;
  animation-fill-mode: forwards;
}

@keyframes slide-in {
  from {
    translate: 0 0;
  }

  to {
    translate: 0 calc(50cqh - 50%);
  }
}

.dialog .chinese-font {
  color: brown;
}

.message-dialog .chinese-font {
